}

.portfolio .portfolio-wrap:hover .portfolio-links {
  opacity: 1;
  bottom: 0;
}

.portfolio-item a:hover {
  cursor: pointer !important;
}

.portfolio-item a {
  cursor: pointer;
  display: block;
  text-decoration: none;
}

.portfolio .portfolio-wrap .portfolio-box {
  width: 1280px;
  height: 200px;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgb(139, 70, 70);
  text-decoration: none;
  border-radius: 20px;
  color: #190666 !important;
  font-size: 30px;
  font-weight: 500;
  padding: 20px;
  font-family: 'Times New Roman', Times, serif;
}

.portfolio .portfolio-wrap .portfolio-box .portfolio-title {
  text-decoration: none;
  text-align: center;
}

.contact {
  padding-bottom: 130px;
}

.contact .info {
  padding: 30px;
  background: #fff;
  width: 100%;
  box-shadow: 0 0 24px 0 rgba(0, 0, 0, 0.12);
}

.contact .info i {
  font-size: 20px;
  color: #149ddd;
  float: left;
  width: 44px;
  height: 44px;
  background: #dff3fc;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50px;
  transition: all 0.3s ease-in-out;
}

.contact .info h4 {
  padding: 0 0 0 60px;
  font-size: 22px;
  font-weight: 600;
  margin-bottom: 5px;
  color: #050d18;
}

.contact .info p {
  padding: 0 0 10px 60px;
  margin-bottom: 20px;
  font-size: 14px;
  color: #173b6c;
}

.contact .info .email p {
  padding-top: 5px;
}

.contact .info .email:hover i,
.contact .info .phone:hover i, 
.contact .info .calendly:hover i{
  background: #149ddd;
  color: #fff;
}